    <div v-else-if="lang===`en`">
            <h4 class="fw-bold"> Before the 2nd Corps entered the fight</h4>
      <p class="fw-bold">
        
Monte Cassino, Ancona, Bologna - these names are most often mentioned when talking about the achievements of the Polish army on the Italian front of World War II. Of course, there is also the name of the 2nd Polish Corps to which these victories belong. The soldiers of General Władysław Anders were not the first in Italy, however, before them the name of Poland was made famous by our sailors, airmen, partisans and commandos.

      </p>
      <p>
        After the Allies defeated the Italian and German troops in North Africa, they turned to Sicily in the summer of 1943. The conquest of this island guaranteed the control of the entire Mediterranean Sea and made it possible to invade the Apennine Peninsula. The invasion armada did not run out of ships and ships under the Polish flag. Besides, seamen from the Polish navy and merchant navy got to know the water routes around Italy earlier, when they sailed in convoys to Malta and Africa. Operation "Husky", that is landing in Sicily, turned out to be lucky for the Polish sailors. The transport ships MS "Batory", MS "Sobieski" and MS "Kościuszko", filled with army and equipment, reached the coast of Sicily without losses. Destroyers - ORP "Krakowiak", ORP "Piorun" and ORP "Ślązak" - were doing just as well, protecting the amphibious assaults with gun fire and repelling enemy air force attacks. The allied landing on the Italian coast was much heavier for the Polish fleet. Among other things, on December 3, 1943, the Luftwaffe made a massive raid on the port of Bari, already occupied by the Allies. At that time, German bombs sank over a dozen transporters, including the Polish MS "Lwów" and MS "Puck". The landing at Anzio and Nettuno turned out to be much happier, with "Batory", "Sobieski" and "Narvik" among 250 units. Polish destroyers did their best during the operations in the Salerno Bay. "Ślązak" led a squadron of Allied destroyers and distinguished himself in rescuing the crew of the sunken British battleship HMS "Warspite", and "Piorun" shot down two German planes.
      </p>
      <h4 class="fw-bold"> "Scary Twins"</h4>
      <p>
        However, Polish submarines gained the greatest fame in the Mediterranean. Two twin ships operated here - ORP "Dzik" and ORP "Sokół". Both were wreaking havoc on the Italian and German navies. The first pool of sinking enemy units was opened by "Dzik" under the command of Commander Bolesław Romanowski, directed to the Mediterranean Sea in May 1943. He soon became successful here. First, a 12,000-ton oil tanker was torpedoed in the Strait of Messina, then a 4,000-man cargo ship in the Aeolian Islands, and two five-thousanders near the port of Bari. The crew of the "Dzika" had the greatest triumph on September 21, 1943 in the waters of Corsica. On that one day, Polish submarines sank 11 German naval vessels with a total capacity of 14,000 tons. They completely paralyzed the German sea evacuation from this island. Captain Jerzy Koziołkowski's twin "Sokół" was not less successful at that time. The Falcon was the first Allied ship to enter the naval port of the Italian navy in Brindisi, from where it convoyed Italian capitulating ships and transports with Italian prisoners to Malta. Having completed this task, he went on a combat patrol to the Adriatic Sea, where in October 1943 he sent two enemy ships to the bottom and severely damaged the third one. In November, he joined the "Dzik" in the Aegean Sea and captain Koziołkowski recorded another seven sinkings in his logbook. Thanks to these victories, the British dubbed the "Terrible Twins". When "Dzik" and "Falcon" left the Mediterranean, the British admiralty said goodbye to their crews with the following words: "Your deeds inspired us, inspired us to admire and trust you for all time.


      </p>
      <h4 class="fw-bold">Skalski and others</h4>
      <p>
        In 1943, the first Polish pilots joined the sailors fighting off the coast of Italy - members of the famous Polish Fighter Team, better known as the "Circus of Skalski", as its allies named after its commander, Captain Captain Stanisław Skalski. After the successes of the "Circus" in Tunisia, the RAF command began to strive for Polish airmen to take part in the Italian campaign being prepared. Each Polish officer was offered a promotion and command of a squadron, and even a squadron. Stanisław Skalski, Eugeniusz Horbaczewski and Maciej Derecki agreed to it. As promised, everyone was promoted. Stanisław Skalski, in the rank of major, commanded the English 601st Fighter Squadron "Country of London", captain pilot Eugeniusz Horbaczewski, called "Dziubek", headed the 43rd Fighter Squadron, and captain pilot Maciej Derecki - headed the squadron of 152nd Fighter Squadron. All these squadrons supported the invasion of Sicily and the fighting of the Allied armies in southern Italy. In September 1943, during the take-off for the next combat flight, Captain Derecki was killed, and Skalski and Horbaczewski returned to Polish fighter squadrons. At the beginning of 1944, two Polish air formations appeared on the Apennine Peninsula: 318 Fighter-Reconnaissance Squadron and 301 Bomber Squadron. Defenders of Warsaw. The pilots of the former cooperated first with the British 8th Army, and then with the 2nd Polish Corps. Air scouts identified and photographed ground targets and corrected artillery fire during attacks on the Gustav Line. These tasks were extremely dangerous, because the Germans concentrated about 70 heavy anti-aircraft guns in this area, not counting the smaller weapons used to fight aircraft. After breaking the Gustav line, the 318 Squadron supported the 2nd Corps along its entire combat route. On the other hand, the 301st Bomb Squadron, after being at the Italian airport in Brindisi in November, was renamed 1856 Special Purpose Squadron and subordinated to the 334 Special Purpose Wing. From then on, Polish Halifaxes and Liberators flew with paratroopers and supplies to support the resistance movement in Albania, Greece, Yugoslavia and other countries of occupied Europe, including Poland. After the outbreak of the Warsaw Uprising, Polish airmen, again under the name of the 301st Bomb Squadron, made 80 flights from Brindisi to Warsaw with supplies for the insurgents.


      </p>
      <h4 class="fw-bold"> Partisans and specials

</h4>
      <p>
        Throughout 1944, the pilots of the 301 Squadron also supported the Italian partisans, which in November 1943 already numbered about 30,000 people. There were many Poles, most often Wehrmacht deserters. According to various estimates, from half a thousand to a thousand Poles fought in the Italian partisan units. In Italy, there were also Polish Cichociemni, sent there for subversive actions by the British Special Operations Executive (SOE - Special Operations Management). Among the paratroopers fighting at the rear of the Italian front were, among others, Captain Władysław Buryn, Captain Roman Kowalik, Lieutenant Henryk Śreniawa-Saganowski and Sergeant Tadeusz Brogowski. On December 2, 1943, the 1st Independent Commando Company of Major Władysław Smrokowski landed in the Italian port of Taranto. This elite branch of the Polish Armed Forces in the West, trained in British commando training centers, was incorporated into the 2 Special Service Brigade. On December 13, Polish commandos arrived in Castel del Giudice on the Sangro River, where they carried out reconnaissance for the British 78th Infantry Division. Before Christmas itself, an 80-strong company was dispatched to the remote village of Pescopennataro, from where it was to patrol the mountain front. On December 21, the Polish headquarters were attacked by nearly 300 German Alpine Riflemen. The commandos, however, were not surprised and repulsed the attack - they killed 20 Germans and captured some of them. Own losses - only three injured. In January 1944, commandos traveled to the Garigliano River to work for the 56th London Infantry Division, which was engaged in heavy fighting with Germany. The Poles set off to fight in the evening of January 17, following the attacking British soldiers. Notes from that day in the unit's combat log: “After crossing the river in a minefield, the company suffers losses: 4 people are wounded. Because the leading battalion of the 56th division did not capture until At 03.30 on January 18, the prescribed bridgehead points and did not take control of the hills beyond the river, the company set off to attack and at At 04.30 it takes over the hill, where after two hours it is released by an infantry battalion ”. After this success, the commandos did not go to their well-deserved rest, but the first platoon hit another hill near the village of Sujo and destroyed a nest of enemy mortars there. At the same time, the second patrol under the command of Captain Stanisław Wołoszowski, deputy major Smrokowski, conquered the Monte Valle Martina hill. The commandos threw Alpine Riflemen out of there and repulsed their counterattack. They killed 30 of them. Unfortunately, Captain Wołoszowski was killed in this fight. After the fighting over Garigliano, the company of Polish commandos was excluded from the British command. The 2nd Polish Corps has just landed in Italy. When his divisions were preparing to take a position at Monte Cassino, the commandos of Major Smrokowski also joined them. Soldiers in green Commando berets began another, equally glorious, chapter of their combat service.
      </p>
