    <div v-else-if="lang===`it`">
            <h4 class="fw-bold"> Prima che il 2 ° Corpo entrasse in battaglia</h4>
      <p class="fw-bold">
        
Monte Cassino, Ancona, Bologna: questi nomi sono più spesso menzionati quando si parla dei risultati dell'esercito polacco sul fronte italiano della seconda guerra mondiale. Ovviamente c'è anche il nome del 2 ° Corpo d'Armata Polacco a cui appartengono queste vittorie. I soldati del generale Władysław Anders non furono i primi in Italia, tuttavia, prima di loro il nome della Polonia fu reso famoso dai nostri marinai, aviatori, partigiani e commando.

      </p>
      <p>
        Dopo che gli Alleati sconfissero le truppe italiane e tedesche in Nord Africa, nell'estate del 1943 si rivolsero in Sicilia. La conquista di quest'isola garantì il controllo dell'intero Mar Mediterraneo e rese possibile l'invasione della penisola appenninica. L'armata d'invasione non rimase a corto di navi e navi sotto la bandiera polacca. Inoltre, i marinai della marina polacca e della marina mercantile hanno conosciuto le rotte marittime intorno all'Italia in precedenza, quando hanno navigato in convogli verso Malta e l'Africa. L'operazione "Husky", che sbarca in Sicilia, si è rivelata fortunata per i marinai polacchi. Le navi da trasporto MS "Batory", MS "Sobieski" e MS "Kościuszko", piene di esercito e attrezzature, hanno raggiunto le coste della Sicilia senza perdite. I cacciatorpediniere - ORP "Krakowiak", ORP "Piorun" e ORP "Ślązak" - stavano facendo altrettanto bene, proteggendo gli attacchi anfibi con colpi di arma da fuoco e respingendo gli attacchi dell'aviazione nemica. Lo sbarco alleato sulle coste italiane fu molto più pesante per la flotta polacca. Tra l'altro, il 3 dicembre 1943, la Luftwaffe compì una massiccia incursione nel porto di Bari, già occupato dagli Alleati. A quel tempo, le bombe tedesche affondarono oltre una dozzina di trasportatori, inclusi gli Stati membri polacchi "Lwów" e "Puck". Molto più felice lo sbarco ad Anzio e Nettuno, con "Batory", "Sobieski" e "Narvik" tra le 250 unità. I cacciatorpediniere polacchi hanno fatto del loro meglio durante le operazioni nella Baia di Salerno. "Ślązak" guidava uno squadrone di cacciatorpediniere alleati e si distinse nel salvare l'equipaggio della corazzata britannica affondata HMS "Warspite", e "Piorun" abbatté due aerei tedeschi.
      </p>
      <h4 class="fw-bold"> "Scary Twins"</h4>
      <p>
        Tuttavia, i sottomarini polacchi hanno guadagnato la più grande fama nel Mediterraneo. Due navi gemelle operavano qui: ORP "Dzik" e ORP "Sokół". Entrambi stavano scatenando il caos nelle marine italiane e tedesche. Il primo gruppo di unità nemiche che affondavano fu aperto da "Dzik" sotto il comando del comandante Bolesław Romanowski, diretto nel Mar Mediterraneo nel maggio 1943. Presto ebbe successo qui. Dapprima una petroliera da 12.000 tonnellate è stata silurata nello Stretto di Messina, poi una nave da carico da 4.000 uomini nelle Isole Eolie e due da cinquemila nei pressi del porto di Bari. L'equipaggio della "Dzika" ebbe il più grande trionfo il 21 settembre 1943 nelle acque della Corsica. Quel giorno, i sottomarini polacchi affondarono 11 navi militari tedesche con una capacità totale di 14.000 tonnellate. Hanno completamente paralizzato l'evacuazione marittima tedesca da quest'isola. Il gemello del capitano Jerzy Koziołkowski "Sokół" non ebbe meno successo in quel momento. Il Falcon è stata la prima nave alleata ad entrare nel porto navale della marina italiana a Brindisi, da dove ha convogliato navi capitolanti italiane e trasporti con prigionieri italiani a Malta. Completato questo compito, si recò in una pattuglia di combattimento nel mare Adriatico, dove nell'ottobre 1943 inviò due navi nemiche sul fondo e danneggiò gravemente la terza. A novembre, si è unito alla "Dzik" nel Mar Egeo e il capitano Koziołkowski ha registrato altri sette affondamenti nel suo diario di bordo. Grazie a queste vittorie, gli inglesi hanno soprannominato i "Terrible Twins". Quando "Dzik" e "Falcon" hanno lasciato il Mediterraneo, l'ammiragliato britannico ha salutato i loro equipaggi con le seguenti parole: "Le tue azioni ci hanno ispirato, ci hanno ispirato ad ammirarti e ad avere fiducia in te per sempre.






      </p>
      <h4 class="fw-bold">Skalski e altri</h4>
      <p>
       Nel 1943, i primi piloti polacchi si unirono ai marinai che combattevano al largo delle coste italiane - membri della famosa squadra di caccia polacchi, meglio conosciuta come "Circo di Skalski", come i suoi alleati chiamavano il suo comandante, il capitano Stanisław Skalski. Dopo i successi del "Circo" in Tunisia, il comando della RAF ha iniziato ad adoperarsi affinché gli aviatori polacchi prendessero parte alla campagna italiana in preparazione. Ad ogni ufficiale polacco è stata offerta la promozione e il comando di uno squadrone e persino di uno squadrone. Stanisław Skalski, Eugeniusz Horbaczewski e Maciej Derecki hanno accettato. Come promesso, tutti sono stati promossi. Stanisław Skalski, nel grado di maggiore, comandava il 601 ° Fighter Squadron inglese "Country of London", il capitano pilota Eugeniusz Horbaczewski, chiamato "Dziubek", a capo del 43 ° Fighter Squadron, e il capitano pilota Maciej Derecki - a capo dello squadrone del 152 ° Fighter Squadron . Tutti questi squadroni sostenevano l'invasione della Sicilia e il combattimento degli eserciti alleati nell'Italia meridionale. Nel settembre 1943, durante il decollo per il successivo volo di combattimento, il capitano Derecki fu ucciso e Skalski e Horbaczewski tornarono negli squadroni di caccia polacchi. All'inizio del 1944, due formazioni aeree polacche apparvero sulla penisola appenninica: 318 Fighter-Reconnaissance Squadron e 301 Bomber Squadron. Difensori di Varsavia. I piloti del primo collaborarono prima con l'8a armata britannica e poi con il 2 ° corpo polacco. Gli esploratori aerei hanno identificato e fotografato obiettivi a terra e corretto il fuoco di artiglieria durante gli attacchi alla linea Gustav. Questi compiti erano estremamente pericolosi, perché i tedeschi concentrarono circa 70 cannoni antiaerei pesanti in quest'area, senza contare le armi più piccole usate per combattere gli aerei. Dopo aver rotto la linea Gustav, lo squadrone 318 ha sostenuto il 2 ° corpo lungo tutta la sua rotta di combattimento. D'altra parte, il 301st Bomb Squadron, dopo essere stato all'aeroporto italiano di Brindisi a novembre, fu ribattezzato 1856 Special Purpose Squadron e subordinato al 334 Special Purpose Wing. Da quel momento in poi, Halifax e Liberatori polacchi volarono con paracadutisti e rifornimenti per sostenere il movimento di resistenza in Albania, Grecia, Jugoslavia e altri paesi dell'Europa occupata, inclusa la Polonia. Dopo lo scoppio della Rivolta di Varsavia, gli aviatori polacchi, sempre sotto il nome di 301 ° Squadrone di bombe, effettuarono 80 voli da Brindisi a Varsavia con rifornimenti per gli insorti.


      </p>
      <h4 class="fw-bold"> Partigiani e speciali

</h4>
      <p>
        Per tutto il 1944 i piloti dello Squadrone 301 affiancarono anche i partigiani italiani, che già nel novembre 1943 contavano circa 30.000 persone. C'erano molti polacchi, il più delle volte disertori della Wehrmacht. Secondo varie stime, nelle unità partigiane italiane combatterono da mezzo mille a mille polacchi. In Italia c'erano anche Cichociemni polacchi, inviati lì per azioni sovversive dal British Special Operations Executive (SOE - Special Operations Management). Tra i paracadutisti che combattevano alle spalle del fronte italiano c'erano, tra gli altri, il capitano Władysław Buryn, il capitano Roman Kowalik, il tenente Henryk Śreniawa-Saganowski e il sergente Tadeusz Brogowski. Il 2 dicembre 1943, la prima compagnia di comando indipendente del maggiore Władysław Smrokowski sbarcò nel porto italiano di Taranto. Questo ramo d'élite delle forze armate polacche in Occidente, addestrato nei centri di addestramento dei commando britannici, fu incorporato nella 2a brigata dei servizi speciali. Il 13 dicembre, i commando polacchi arrivarono a Castel del Giudice sul fiume Sangro, dove effettuarono le ricognizioni per la 78a divisione di fanteria britannica. Prima del Natale stesso, una compagnia di 80 persone fu inviata nel remoto villaggio di Pescopennataro, da dove doveva pattugliare il fronte montuoso. Il 21 dicembre, il quartier generale polacco è stato attaccato da quasi 300 fucilieri alpini tedeschi. I commando, tuttavia, non furono sorpresi e respinsero l'attacco: uccisero 20 tedeschi e ne catturarono alcuni. Perdite proprie - solo tre feriti. Nel gennaio 1944, i commando si recarono sul fiume Garigliano per lavorare per la 56a divisione di fanteria di Londra, impegnata in pesanti combattimenti con la Germania. I polacchi partirono a combattere la sera del 17 gennaio, al seguito dell'attacco dei soldati britannici. Note di quel giorno nel registro di combattimento dell'unità: “Dopo aver attraversato il fiume in un campo minato, la compagnia subisce delle perdite: 4 persone sono ferite. Poiché il battaglione leader della 56a divisione non ha catturato fino alle 03.30 del 18 gennaio, i punti di testa di ponte prescritti e non ha preso il controllo delle colline oltre il fiume, la compagnia è partita all'attacco e alle 04.30 prende il controllo della collina, dove dopo due ore viene rilasciato da un battaglione di fanteria ”. Dopo questo successo, i commando non andarono al loro meritato riposo, ma il primo plotone colpì un'altra collina vicino al villaggio di Sujo e lì distrusse un nido di mortai nemici. Allo stesso tempo, la seconda pattuglia al comando del capitano Stanisław Wołoszowski, vice maggiore Smrokowski, conquistò il monte Valle Martina. I commando gettarono fuori di lì i fucilieri alpini e respinsero il loro contrattacco. Ne hanno uccisi 30. Sfortunatamente, il capitano Wołoszowski è stato ucciso in questo combattimento. Dopo i combattimenti per il Garigliano, la compagnia dei commando polacchi fu esclusa dal comando britannico. Il 2 ° Corpo d'Armata Polacco è appena sbarcato in Italia. Quando le sue divisioni si stavano preparando a prendere posizione a Montecassino, si unirono a loro anche i commando del maggiore Smrokowski. I soldati con i berretti da commando verdi iniziarono un altro, altrettanto glorioso, capitolo del loro servizio di combattimento.
      </p>
